var ExternalLink = {
	open: function ( url ) {
		var el = document.getElementById( 'external_link' );

		if ( el === null ) {
			el = document.createElement( 'a' );
		}

		el.href = url;
		el.target = '_blank';

		document.body.appendChild( el );
		el.click();

		el.remove();
	}
};

export default ExternalLink;