import React from 'react';
import './PlayerPreloader.css';


export default class PlayerPreloader extends React.Component {
	constructor( props ) {
		super( props );
	}

	componentDidMount() {
		this.props.toggle( false );
	}

	componentDidUpdate() {
		this.props.toggle( this.props.active );
	}

	render() {
		return (
			<div id="player_preloader" className={this.props.active && "player-preloader--active"} />
		);
	}
}