import React from 'react';
import './MenuItem.css';


export default function MenuItem( { icon, text, onClick, close, iconColor, children, needClose, ...restProps } ) {
	const click = ( event ) => {
		onClick( event );
		if ( needClose ) close();
	}

	return (
		<div className="c-menu-buttons__item" onClick={click} {...restProps}>
			<div className={"c-menu-buttons__icon c-menu-buttons__icon--outlined c-icon--vk c-category__icon--" + iconColor}>{icon}</div>
			<div className="c-menu-buttons__text">{text}</div>
		</div>
	);
}


