import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { Root, View, Epic, Tabbar, Panel } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


// Routing and navigation
import Router from './service/Router.js';

// My components
import './styles/styles.css';
import { Player, Preloader, Offline, Notice, PlayerPreloader } from './components/index.js';

// Panels
import Main from './panels/Main';
import PlayerView from './panels/PlayerView';
import Search from './panels/Search';
import About from './panels/About';


// Audio
import AudioPlayer from './service/AudioPlayer.js';
import Stations from './Stations.js';


import LocalStorage from './LocalStorage.js';
import Config from './Config.js';
import VK from './VK.js';
import Theme from './Theme.js';



var router = new Router();
var audioPlayer = new AudioPlayer( Stations )



class App extends React.Component {
	constructor(props) {
		super(props);

		var settings = LocalStorage.getJSON( 'settings' );

		this.state = {
			activeView: 'epic',
			activeStory: 'mainView',
			activePanel: 'main',
			activeModal: null,
			popoutOpened: false,
			activeModalName: null,
			lastState: null,
			notice: null,
			noticeIsActive: false,

			// Audio player
			audioPaused: true,
			audioVolume: 1,
			audioStation: {},

			activePlayerPreloader: false,

			currentAction: null,

			accessToken: null,

			favorite: [],
			hidden: [],
			settings: settings ? settings : {
				show_hidden: false,
				auto_theme: true,
				theme: 'client_light'
			}
		};

		this.routes = {
			'main': {
				'view': 'epic',
				'panel': 'main',
				'popout': null
			},

			'search': {
				'view': 'epic',
				'panel': 'search',
				'popout': null
			},

			'player': {
				'view': 'playerView',
				'panel': null,
				'popout': null
			},

			'about': {
				'view': 'epic',
				'panel': 'about',
				'popout': null
			}
		};


		// Offline and offline event listeners
		window.addEventListener( 'online', ( event ) => {
			this.toggleOfflineNotifer( false );
		} );

		window.addEventListener( 'offline', ( event ) => {
			this.toggleOfflineNotifer( true );
			this.togglePreloader( false );
		} );

		window.addEventListener( 'focus', ( event ) => {
			this.toggleOfflineNotifer( !window.navigator.onLine );
			Theme.setActionBar();
		} );


		// AudioPlayer event listeners
		window.addEventListener( 'AudioPlayerInitializationSuccess', ( event ) => { console.log( event ) } );
		window.addEventListener( 'AudioPlayerSetStation', ( event ) => {
			this.setState( {
				audioStation: event.detail.station
			} );
		} );

		window.addEventListener( 'AudioPlayerPause', ( event ) => {
			this.setState( {
				audioPaused: true
			} );
		} );

		window.addEventListener( 'AudioPlayerPlay', ( event ) => {
			this.setState( {
				audioPaused: false
			} );
		} );

		window.addEventListener( 'AudioPlayerCanPlay', ( event ) => {
			this.togglePlayerPreloader( false );
			this.togglePreloader( false );
		} );

		window.addEventListener( 'AudioPlayerWaiting', ( event ) => {
			this.togglePlayerPreloader( true );
		} );

		window.addEventListener( 'AudioPlayerError', ( event ) => {
			this.togglePlayerPreloader( false );
			this.showNotice( 'При воспроизведении станции произошла ошибка. Пожалуйста, повторите попытку позже.' );
		} );

		window.addEventListener( 'AudioPlayerVolumeChanged', ( event ) => {
			this.setState( {
				audioVolume: event.detail.volume
			} );
		} );


		// Router event listener

		window.addEventListener( 'popstate', ( event ) => {
			event.preventDefault();

			if ( this.state.activeModal ) {
				return;
			}

			this.goBack();
		} );


		// Other events

		window.addEventListener( 'FavoriteNew', ( event ) => {
			this.togglePreloader( true );

			this.storageGet( ['favorite'], ( res ) => {
				try {
					var favorite = JSON.parse( res[0].value );
				} catch( e ) {
					console.error( e );
				}

				var newItemId = event.detail.newItemId;

				var needUpdateFavoritePlaylist = this.AudioPlayer._getState().playlist == window.favorite;

				this.togglePreloader( false );
				if ( favorite.indexOf( newItemId ) !== -1 ) return;

				favorite.push( newItemId );

				window.favorite = favorite;
				this.setState( { favorite: favorite } );

				this.storageSet( 'favorite', JSON.stringify( favorite ), false, () => {} );

				if ( needUpdateFavoritePlaylist ) this.setPlaylist( window.favorite );
				this.setDefaultPlaylist();
				this.showNotice( 'Радиостанция добавлена в избранное' );
			} );
		} );


		window.addEventListener( 'FavoriteDelete', ( event ) => {
			this.togglePreloader( true );

			this.storageGet( ['favorite'], ( res ) => {
				try {
					var favorite = JSON.parse( res[0].value );
				} catch( e ) {
					console.error( e );
				}

				var deleteItemId = event.detail.deleteItemId;
				var i = favorite.indexOf( deleteItemId );

				var needUpdateFavoritePlaylist = this.AudioPlayer._getState().playlist == window.favorite;

				this.togglePreloader( false );
				// if ( i == -1 ) return;
				favorite.splice( i, 1 );

				window.favorite = favorite;
				this.setState( { favorite: favorite } );

				this.storageSet( 'favorite', JSON.stringify( favorite ), false, () => {} );
	
				if ( needUpdateFavoritePlaylist ) this.setPlaylist( window.favorite );
				this.setDefaultPlaylist();
				this.showNotice( 'Радиостанция удалена из избранного' );
			} );
		} );

		window.addEventListener( 'HiddenNew', ( event ) => {
			this.togglePreloader( true );

			this.storageGet( ['hidden'], ( res ) => {
				try {
					var hidden = JSON.parse( res[0].value );
				} catch( e ) {
					console.error( e );
				}

				var newItemId = event.detail.newItemId;


				this.togglePreloader( false );
				if ( hidden.indexOf( newItemId ) !== -1 ) return;

				hidden.push( newItemId );

				window.hidden = hidden;
				this.setState( { hidden: hidden } );

				this.storageSet( 'hidden', JSON.stringify( hidden ), false, () => {} );
				this.setDefaultPlaylist();
				this.showNotice( 'Радиостанция скрыта из общего списка' );
			} );
		} );

		window.addEventListener( 'HiddenDelete', ( event ) => {
			this.togglePreloader( true );

			this.storageGet( ['hidden'], ( res ) => {
				try {
					var hidden = JSON.parse( res[0].value );
				} catch( e ) {
					console.error( e );
				}

				var deleteItemId = event.detail.deleteItemId;
				var i = hidden.indexOf( deleteItemId );

				this.togglePreloader( false );
				//if ( i == -1 ) return;
				hidden.splice( i, 1 );

				window.hidden = hidden;
				this.setState( { hidden: hidden } );

				this.storageSet( 'hidden', JSON.stringify( hidden ), false, () => {} );
				this.setDefaultPlaylist();
				this.showNotice( 'Радиостанция теперь снова показывается в общем списке' );
			} );
		} );
		


		window.addEventListener( 'SettingsSaved', ( event ) => {
			this.setState( {settings: event.detail} );
		} );


		var parts = window.location.href.split( '?' );
		this.startParams = router.parseParams( parts[1] );

		this.platform = this.startParams.vk_platform;

		var platforms = {
			'mobile_iphone': 'ios',
			'mobile_android': 'android',
			'mobile_web': 'mobile_web',
			'desktop_web': 'web'
		};

		if ( this.platform in platforms ) {
			document.body.classList.add( platforms[this.platform] );
			window.platform = platforms[this.platform];
		}



		window.hidden = [];
		window.favorite = [];

		this.Router = router;
		this.AudioPlayer = audioPlayer;

		this.history = [];
		this.statesHistory = [];

		this.scrollbarWidth = this.getScrollbarWidth();

		this.storageCallback = null;
		this.storageSetCallback = null;
	}

	componentDidMount() {
		this.togglePreloader( true );
		this.autotheme();

		this.setDefaultPlaylist();


		if ( connect.supports( 'VKWebAppStorageGet' ) ) {

			this.storageGet( ['favorite', 'hidden'], function ( keys ) {
				var favorite = favorite;
				var hidden = hidden;

				keys.forEach( ( item ) => {
					try {
						if ( item.key == 'favorite' ) favorite = JSON.parse( item.value );
						if ( item.key == 'hidden' ) hidden = JSON.parse( item.value );
					} catch( e ) {
						console.error( e );
					}
				} );

				window.favorite = favorite;
				window.hidden = hidden;

				this.setState( {
					favorite: favorite,
					hidden: hidden
				} );
			} );
		} else {
			this.showNotice( 'Пожалуйста, обновите приложение ВКонтакте, иначе некоторые функции могут не работать.' );
		}

		connect.subscribe( ( event ) => {
			var detail = event.detail;
			var data = detail.data;
			var type = detail.type;

			console.log( type, data );

			if ( type == 'VKWebAppUpdateConfig' ) {
				this.theme = data.scheme;
				window.theme = data.scheme;
				this.autotheme();

				//Connect.send( 'VKWebAppSetViewSettings', config.action_bar );
			} else if ( type == 'VKWebAppStorageGetResult' ) {
				this.storageExecCallback( data.keys );
			} else if ( type == 'VKWebAppAccessTokenReceived' ) {
				
			} else if ( type == 'VKWebAppStorageSetFailed' ) {
				this.togglePreloader( false );
				this.showNotice( 'Произошла неизвестная ошибка' );
			} else if ( type == 'VKWebAppStorageSetResult' ) {
				this.togglePreloader( false );
				this.storageSetExecCallback( data.result );
			} else if ( type == 'VKWebAppCallAPIMethodResult' ) {
				this.VK._execCallback( data.request_id, data.response );
			}
		} );


		if ( this.Router.data.get.station ) {
			this.AudioPlayer._set( this.Router.data.get.station );
		} else {
			var last_station = LocalStorage.get( 'last_station' );
			if ( !last_station ) last_station = 1;

			this.AudioPlayer._set( last_station );
		}

		var initialVolume = LocalStorage.get( 'volume' );
		if ( initialVolume !== null ) this.setVolume( initialVolume );

		console.log( 'Initial volume:', initialVolume );

		let hash = this.Router.data.hash;

		if ( hash != '' && hash != '/main' ) {
			this.navigate( '/main' );
		}

		setTimeout( () => this.navigate( hash ), 100 ); // При загрузке приложения открываем вьюшку соответствующую хешу
	}

	storageGet = ( keys, callback ) => {
		// устанавливаем действие, чтобы потом можно было обрабатывать события storage и мы знали что делать с данными

		this.storageCallback = callback;
		connect.send( 'VKWebAppStorageGet', {'keys': keys} );
	}

	storageSet = ( key, value, glob, callback ) => {
		this.storageSetCallback = callback;
		connect.send( 'VKWebAppStorageSet', {
			key: key,
			value: value,
			global: glob
		} );
	}

	storageSetExecCallback = ( result ) => {
		if ( typeof this.storageSetCallback != 'function' ) return;
		this.storageSetCallback( result );

		this.storageSetCallback = null;
	}

	storageExecCallback = ( keys ) => {
		if ( typeof this.storageCallback != 'function' ) return;
		this.storageCallback( keys );

		this.storageCallback = null;
	}

	navigate = ( to ) => {
		console.log( to );

		if ( typeof to != 'string' ) {
			to = to.currentTarget.dataset.to;
		}

		if ( !to ) {
			to = '/main';
		}

		var navData = this.Router.parseHash( to );
		var dir = navData.dir;


		if ( navData.dir in this.routes ) {
			let route = this.routes[dir];
			let uri = to;

			this.Router.navigate( uri );
			this.toggleDocumentScrolling( true );

			let newState = {
				activeView: route.view
			};

			if ( route.panel ) {
				newState.activePanel = route.panel ? route.panel : '';
			}

			newState.activeModal = route.popout;

			if ( this.history[this.history.length - 1] != dir ) {
				this.history.push( dir );
				this.statesHistory.push( {} );
			}

			console.log( this.history );

			this.setState( newState );
			return;
		}
	}

	goBack = () => {
		if ( this.history.length == 1 ) return;
		this.history.pop();
		this.statesHistory.pop();

		let index = this.history.length - 1;


		console.log( this.statesHistory );

		if ( this.statesHistory[index] ) {
			this.setState( {lastState: this.statesHistory[index] } );
		}

		this.navigate( '/' + this.history[index] );
	}


	popoutClose = () => {
		this.setState( {
			activeModal: null,
			popoutOpened: false
		} );

		this.toggleDocumentScrolling( true );
		Theme.setActionBar();
	}

	setTheme = ( theme ) => {
		if ( theme == 'client_dark' ) {
			document.body.classList.add( 'theme-dark' );
		} else {
			document.body.classList.remove( 'theme-dark' );
		}
	}

	autotheme = () => {
		if ( this.state.settings.auto_theme ) {
			Theme.set( this.theme );
		} else {
			Theme.set( this.state.settings.theme );
		}
	}

	openModal = ( modalName, onClose, props ) => {
		if ( !props ) {
			var props = {
				onClose: onClose
			}
		} else {
			props.onClose = onClose;
		}

		if ( typeof modalName == 'function' ) {
			var modal = React.createElement(
				modalName,
				props,
				null
			);			
		} else {
			var modal = modalName;
		}

		this.toggleDocumentScrolling( false );
		window.history.pushState( null, null );

	

		this.setState( {
			activeModal: modal,
			popoutOpened: true
		} );

		//Theme.setPopupActionBar();
	}

	componentDidUpdate( prevProps, prevState ) {
		// if ( this.state.popoutOpened && this.state.popoutOpened != prevState.popoutOpened ) {
		// 	Theme.setPopupActionBar();
		// } else {
		// 	Theme.setActionBar();
		// }

		if ( prevState.audioStation != this.state.audioStation ) {
			LocalStorage.save( 'last_station', this.state.audioStation.id );
		}
	}


	setStation = ( e ) => {
		let id = e.currentTarget.dataset.id;
		this.AudioPlayer.play( id );
	}

	setVolume = ( volume ) => {
		LocalStorage.save( 'volume', volume );
		this.AudioPlayer.setVolume( volume );
	}

	toggle = () => {
		this.AudioPlayer.toggle();
	}

	prev = () => {
		this.togglePreloader( true );
		this.AudioPlayer.prev();
	}

	next = () => {
		this.togglePreloader( true );
		this.AudioPlayer.next();
	}

	setPlaylist = ( playlist ) => {
		this.AudioPlayer._setPlaylist( playlist );
	}

	clearPlaylist = () => {
		this.AudioPlayer._clearPlaylist();
	}

	setDefaultPlaylist = () => {
		var defaultPlaylist = {};

		Object.keys( Stations ).forEach( ( item, i ) => {
			if ( window.hidden.indexOf( parseInt( item ) ) !== -1 ) return;
			if ( window.favorite.indexOf( parseInt( item ) ) !== -1 ) return;

			defaultPlaylist[item] = Stations[item];
		} );

		this.defaultPlaylist = defaultPlaylist;
		this.AudioPlayer._setDefaultPlaylist( defaultPlaylist );
	}


	saveState = ( view, state ) => {
		let index = this.history.indexOf( view );

		if ( index !== -1 ) {
			this.statesHistory[index] = state;
		}
	}


	toggleOfflineNotifer = ( enabled ) => {
		if ( this.state.isOffline == enabled ) return;

		this.setState( {
			isOffline: enabled
		} );
	}

	togglePreloader = ( enabled ) => {
		if ( this.state.activePreloader == enabled ) return;

		this.setState( {
			activePreloader: enabled
		} );

		this.toggleDocumentScrolling( !enabled );
	}

	togglePlayerPreloader = ( enabled ) => {
		if ( this.history[this.history.length-1] != 'main' && enabled ) return; // Показываем только на главном экране
		if ( this.state.activePlayerPreloader == enabled ) return;

		this.setState( {
			activePlayerPreloader: enabled
		} );
	}

	showNotice = ( text ) => {
		this.setState( {
			notice: text,
			noticeIsActive: true
		} );
	}

	hideNotice = () => {
		this.setState( {
			notice: null,
			noticeIsActive: false
		} );
	}

	toggleDocumentScrolling = ( enabled ) => {
		this.documentScrolling = enabled;
		var body = document.getElementsByTagName( 'body' )[0];

		if ( enabled ) {
			body.style.paddingRight = '';
			body.classList.remove( 'noscroll' );

			window.removeEventListener( 'touchmove', this.preventTouch );
		} else {
			if ( document.getElementsByClassName( 'Panel__in' )[0].offsetHeight > document.body.offsetHeight ) {
				body.style.paddingRight = this.scrollbarWidth + 'px';
			}

			body.classList.add( 'noscroll' );

			window.addEventListener( 'touchmove', this.preventTouch, { passive: false } );
		}
	}

	getScrollbarWidth = () => {
		let div = document.createElement('div');

		div.style.overflowY = 'scroll';
		div.style.width = '50px';
		div.style.height = '50px';

		// мы должны вставить элемент в документ, иначе размеры будут равны 0
		document.body.append(div);
		let scrollWidth = div.offsetWidth - div.clientWidth;

		div.remove();

		return scrollWidth;
	}

	preventTouch = ( event ) => {
		if ( !event ) return false;
		while ( event.originalEvent ) event = event.originalEvent;
		event.preventDefault();
	};


	render() {
		return (
			<div id="AppContainer">
				<Preloader active={this.state.activePreloader} toggle={this.toggleDocumentScrolling} />
				<Offline active={this.state.isOffline} />
				<Notice text={this.state.notice} onClose={this.hideNotice} isActive={this.state.noticeIsActive} />
				<PlayerPreloader active={this.state.activePlayerPreloader} toggle={this.togglePlayerPreloader} />

				<Root activeView={this.state.activeView} popout={this.state.activeModal}>
					<Epic id="epic" activeStory="mainView" tabbar={
						<Tabbar>
							<Player onItemClick={this.navigate}
							        data-to="/player"
							        icon={this.state.audioStation.img}
							        title={this.state.audioStation.name}
							        subtitle={this.state.audioStation.genre}
							        right={this.state.audioPaused ? 'play' : 'pause'}
							        additionalIcon={this.state.audioVolume == 0 ? 'mute' : '' }
							        additionalIconColor="red"
							        onRightIconClick={this.toggle}
							/>
						</Tabbar>
					}>
						<View id="mainView" activePanel={this.state.activePanel} header={false}>
							<Main id="main"
								  hidden={this.state.hidden}
								  favorite={this.state.favorite}
								  showHidden={this.state.settings.show_hidden}
								  audioPlayer={this.AudioPlayer}
								  isPlaying={!this.state.audioPaused}
								  navigate={this.navigate}
								  clearPlaylist={this.clearPlaylist}
								  setStation={this.setStation}
								  setPlaylist={this.setPlaylist}
								  station={this.state.audioStation}
								  toggle={this.toggle}
								  openModal={this.openModal}
								  onModalClose={this.popoutClose} />

							<Search id="search"
									hidden={this.state.hidden}
								    favorite={this.state.favorite}
									audioPlayer={this.AudioPlayer}
									isPlaying={!this.state.audioPaused}
									state={this.state.lastState}
									saveState={this.saveState}
									navigate={this.navigate}
									setStation={this.setStation}
									setPlaylist={this.setPlaylist}
									station={this.state.audioStation}
									toggle={this.toggle}
									openModal={this.openModal}
									onModalClose={this.popoutClose}
									goBack={this.goBack} />

							<About id="about"
								   navigate={this.navigate}
								   goBack={this.goBack} />
						</View>

					</Epic>

					<View id="playerView" activePanel="player" header={false}>
						<PlayerView id="player"
									openModal={this.openModal}
								    onModalClose={this.popoutClose}
									audioPlayer={this.AudioPlayer}
									station={this.state.audioStation}
									paused={this.state.audioPaused}
									navigate={this.navigate}
									goBack={this.goBack}
									setVolume={this.setVolume}
									volume={this.state.volume}
									toggle={this.toggle}
									prev={this.prev}
									next={this.next} />
					</View>

					<View id="placeholderView" activePanel="placeholderPanel" header={false}>
						<Panel id="placeholderPanel" />
					</View>
				</Root>
			</div>
		);
	}
}

export default App;
