import React from 'react';
import { Panel } from '@vkontakte/vkui';
import { Header, Content, Category, CategoryList, Input, H, Subtitle, Center, CenterLogo } from '../components/index.js';

import appLogo from './../images/150.jpg';
import Config from './../Config.js'
import ExternalLink from './../ExternalLink.js'


class About extends React.Component {
	constructor( props ) {
		super( props );	
	}

	link = () => {
		var link = 'https://vk.com/' + Config.appGroupScreenName;
		ExternalLink.open( link );
	}

	faq = () => {
		var link = 'https://vk.com/@radio_app-faq';
		ExternalLink.open( link );
	}

	donate = () => {
		var link = 'https://vk.com/app6471849_-162510713';
		ExternalLink.open( link );
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<Header icon="browser_back" onClick={this.props.goBack}>О приложении</Header>
				<Content>
					<br />
				
					<Center>
						<CenterLogo src="/images/150.jpg" />
						<H level="3">Радио</H>
						<Subtitle>Любимые радиостанции онлайн.</Subtitle>
					</Center>
					<br />

					<Category title="Версия приложения"
							  subtitle="v1.2.09 Public Beta"
							  left="info_outline"
							  iconColor="black"
							  leftType="icon"
					 />

					<Category title="Сообщество приложения"
							  subtitle="Онлайн радио"
							  additionalIcon="like"
							  additionalIconColor="red"
							  left={appLogo}
							  leftType="img"
							  onClick={this.link}
							  data-to="https://vk.com/radio_app"
					/>

					<Category title="FAQ"
							  subtitle="Часто задаваемые вопросы"
							  left="help_outline"
							  leftType="icon"
							  onClick={this.faq}
					/>

					<Category title="Отправить деньги"
							  subtitle="На развитие сервиса"
							  left="money_transfer"
							  leftType="icon"
							  hide="hide-item--ios"
							  onClick={this.donate}
					/>
				</Content>
			</Panel>
		);
	}
};


export default About;
