import React from 'react';
import './ModalRoot.css';

import { Touch } from '@vkontakte/vkui';


class ModalRoot extends React.Component {
	constructor( props ) {
		super( props );

		this.position = props.position;
		this.class = ' popup_' + ( this.position ? this.position : 'top' );

		this.state = {
			opened: true
		};

		this.documentScrolling = true;
		window.addEventListener( 'popstate', this.onPopState );
	}

	onPopState = ( event ) => {
		event.preventDefault();
		this.close();
	}

	onPopupClose = () => {

	}

	maskClick = ( event ) => {
		this.close();
	}

	close = () => {
		if ( !this.state.opened ) return;

		this.setState( {
			opened: false
		} );

		if ( typeof this.props.onClose == 'function' ) {
			this.timeout = setTimeout( () => {
				this.props.onClose();
			}, 150 );
		}
	}

	componentDidMount() {
		this.wrapper = document.getElementById( 'popup_wrapper' );
		this.popup = document.getElementById( 'popup' );
		let height = this.popup.offsetHeight;

		if ( height + 65 < window.screen.height ) {
			this.toggleDocumentScrolling( false );
		} else {
			this.toggleDocumentScrolling( true );
		}
	}

	componentDidUpdate() {
		if ( !this.props.opened ) this.close();
	}

	componentWillUnmount() {
		this.toggleDocumentScrolling( true );
		window.removeEventListener( 'popstate', this.onPopState );
		clearTimeout( this.timeout );
	}

	toggleDocumentScrolling( enabled ) {
		if ( this.documentScrolling === enabled ) return;
		this.documentScrolling = enabled;

		if ( enabled ) {
			document.body.overflow = 'auto';
			window.removeEventListener( 'touchmove', this.preventTouch );
		} else {
			document.body.overflow = 'hidden';
			window.addEventListener( 'touchmove', this.preventTouch, { passive: false } );
		}
	}

	preventTouch = ( event ) => {
		if ( !event ) return false;
		while ( event.originalEvent ) event = event.originalEvent;
		event.preventDefault();
	};

	onStart = ( event ) => {
		this.computedStyle = parseInt( getComputedStyle( this.popup ).marginTop );
		this.popup.classList.remove( 'popup--animated' );
	}

	onMove = ( event ) => {
		// Отключаем, чтобы нельзя было мышкой перетаскивать
		if ( event.originalEvent.type == 'mousemove' ) return;

		if ( this.wrapper.scrollTop > 0 || event.shiftY < 0 ) return;

		if ( this.popup.offsetHeight > window.screen.height  ) {
			this.popup.style.marginTop = Math.abs( window.screen.height - this.popup.offsetHeight ) + this.computedStyle + event.shiftY + 'px';
			return;
		}

		this.popup.style.transform = 'translateY( ' + event.shiftY + 'px )';
		//this.popup.style.marginTop = window.screen.height - 65 - this.popup.offsetHeight + event.shiftY + 'px';
	}

	onEnd = ( event ) => {
		// Отключаем, чтобы нельзя было мышкой перетаскивать
		if ( event.originalEvent.type == 'mouseup' ) return;

		if ( this.wrapper.scrollTop > 0 ) return;

		if ( event.shiftY > 100 ) {
			this.close();
			return;
		}

		this.popup.classList.add( 'popup--animated' );
		this.popup.style.transform = 'translateY( 0px )';

		//this.popup.style.marginTop = this.computedStyle + 'px';
	}

	render() {
		const childrenWithProps = React.Children.map( this.props.children, child =>
	    	React.cloneElement( child, { onClose: this.close, ref: this.popupRef } )
	    );

		return (
			<Touch onStart={this.onStart}
				   onMove={this.onMove}
				   onEnd={this.onEnd}>
			<div className={"popup_wrapper" + this.class + ( !this.state.opened ? ' popup_wrapper_hide' : '')} id="popup_wrapper">
				<div className="popup_mask" onClick={this.maskClick} />
					<div className="popup_bottom_wrapper" onClick={this.onPopupClose}>
							{childrenWithProps}
					</div>
			</div>
			</Touch>
		);
	}
}

export default ModalRoot;