import React from 'react';
import { ModalRoot, Modal, H, SecondaryButton, Center } from '../../components/index.js';

import './QR.css';

import qr from '@vkontakte/vk-qr';

export default class QR extends React.Component {
	constructor( props ) {
		super( props );

		this.state = {
			opened: true,
			buttonCopied: false
		};

		this.text = this.props.text;
	}

	closeModal = () => {
		this.props.onClose();
	}

	close = () => {
		this.setState( {opened: false} );
	}

	copy = ( e ) => {
		e.preventDefault();
		var targetId = 'qr__link';

		var copyText = document.getElementById( "qr__link" );
		copyText.select();

		document.execCommand( "copy" );
		copyText.blur();

		this.changeButtonText();
	}

	changeButtonText = () => {
		this.setState( { buttonCopied: true } );
	    this.timeout = setTimeout( () => {
	    	this.setState( { buttonCopied: false } );
	    }, 1000 );
	}

	componentWillUnmount() {
		clearTimeout( this.timeout );
	}

	render() {
		const QRSvg = qr.createQR( this.text, 180, 'qr-code', false );

		return (
			<ModalRoot position="center" onClose={this.closeModal} opened={this.state.opened}>
				<Modal>
					<Center>
						<H level="3">QR-код</H>
						<br />

						<div className="qr__container">
							<div className="qr" dangerouslySetInnerHTML={{__html: QRSvg}}></div>
						</div>

						<p>Отсканируйте QR-код с помощью камеры.</p>

						<input type="text" className="qr__link" id="qr__link" value={this.text} />

						<SecondaryButton onClick={this.copy}>{this.state.buttonCopied ? 'Скопировано' : 'Скопировать ссылку'}</SecondaryButton>
					</Center>
				</Modal>
			</ModalRoot>
		);
	}
}