import React from 'react';
import connect from '@vkontakte/vkui-connect';

import Stations from './Stations.js';
import AudioPlayer from './service/AudioPlayer.js';

import GroupNone from './modals/GroupNone/GroupNone.js';
import QR from './modals/QR/QR';
import Menu from './modals/Menu/Menu';

import Menus from './Menus.js';
import Config from './Config.js';

import ExternalLink from './ExternalLink.js';



class Actions {
	constructor( AudioPlayer, parentThis ) {
		this.audioPlayer = AudioPlayer;
		this.parentThis = parentThis;

		this.Menus = new Menus( this );
	}

	isPlaying = () => {
		return !this.audioPlayer._getState().paused;
	}

	isFavorite = ( id ) => {
		return ( window.favorite && window.favorite.indexOf( parseInt( id ) ) !== -1 );
	}

	isHidden = ( id ) => {
		return ( window.hidden && window.hidden.indexOf( parseInt( id ) ) !== -1 );
	}

	play = ( e ) => {
		let id = e.currentTarget.dataset.id;
		this.audioPlayer.play( id );
		this.parentThis.closeModal();
	}

	pause = ( e ) => {
		this.audioPlayer.pause();
		this.parentThis.closeModal();
	}

	getFavorite = () => {

	}

	openShare = ( e ) => {
		let id = e.currentTarget.dataset.id;

		var menu = [
			this.Menus.showQRCode,
			this.Menus.wallpost,
			this.Menus.shareOther
		];


		this.parentThis.props.openModal( <Menu items={menu} data-id={id} onClose={this.parentThis.closeModal} />, this.parentThis.closeModal );
	}

	addToFavorite = ( e ) => {
		var id = parseInt( e.currentTarget.dataset.id );

		if ( window.favorite.indexOf( id ) !== -1 ) return;

		window.favorite.push( id );

		this._sendEvent( 'FavoriteNew', {
			favorite: window.favorite,
			newItemId: id
		} );
	}

	deleteToFavorite = ( e ) => {
		var id = parseInt( e.currentTarget.dataset.id );

		this._sendEvent( 'FavoriteDelete', {
			favorite: window.favorite,
			deleteItemId: id
		} );
	}

	addToHidden = ( e ) => {
		var id = parseInt( e.currentTarget.dataset.id );
		if ( window.hidden.indexOf( id ) !== -1 ) return;

		window.hidden.push( id );

		this._sendEvent( 'HiddenNew', {
			hidden: window.hidden,
			newItemId: id
		} );
	}

	deleteFromHidden = ( e ) => {
		var id = parseInt( e.currentTarget.dataset.id );

		this._sendEvent( 'HiddenDelete', {
			hidden: window.hidden,
			deleteItemId: id
		} );
	}

	update = ( e ) => {
		let id = e.currentTarget.dataset.id;
		this.audioPlayer.play( id );
	}

	group = ( e ) => {
		let id = e.currentTarget.dataset.id;

		if ( this.audioPlayer.stations[id].group ) {
			ExternalLink.open( this.audioPlayer.stations[id].group );
			return;
		}

		this.parentThis.props.openModal( GroupNone, this.parentThis.props.onModalClose );
	}

	wallpost = ( e ) => {
		var id = e.currentTarget.dataset.id;
		var station = Stations[id];

		var message = '';
		
		if ( !station ) {
			message = 'Слушай радио онлайн вместе со мной: ' + 'https://vk.com/app' + Config.appId;
		} else {
			message = 'Я слушаю радио ' + station.name + ".\r\n" + 'Слушай вместе со мной! ' + 'https://vk.com/app' + Config.appId + '#/player?station=' + id;
		}

		connect.send( 'VKWebAppShowWallPostBox', {message: message} );
	}

	shareOther = ( e ) => {
		var id = e.currentTarget.dataset.id;
		var link = 'https://vk.com/app' + Config.appId + '#/player?station=' + id;

		connect.send( "VKWebAppShare", {link: link} );
	}

	showPlayerQRCode = ( e ) => {
		let id = e.currentTarget.dataset.id;		
		let text = 'https://vk.com/app' + Config.appId + '#/player?station=' + id;
		console.log( text );

		this.parentThis.props.openModal( <QR text={text} onClose={this.parentThis.closeModal} />, this.parentThis.props.onModalClose );
	}

	_sendEvent = ( type, detail ) => {
		let event = new CustomEvent( type, {
			bubbles: true,
			cancelable: true,
			detail: detail
		} );

		window.dispatchEvent( event );
	}
};

export default Actions;