import Connect from '@vkontakte/vkui-connect';

class Router {
	constructor() {
		this.data = this.parseHash();
		this.prevState = {};

		window.addEventListener( 'popstate', ( event ) => {
			console.log( 'Router: Popstate event', event );
			event.preventDefault();

			this.prevState = event.state;
			this.data = this.parseHash();

			this._sendEvent( 'RouterPopState', {data: this.data} );
		} );

		console.log( 'Router Initialization success. Current hash:', this.data.hash );
	}

	parseHash = ( locationHash ) => {
		if ( !locationHash ) {
			locationHash = window.location.hash;
		}

		locationHash = locationHash.substr( 0, 1 ) == '#' ? locationHash.substr( 1 ) : locationHash;
		locationHash = decodeURIComponent( locationHash ); // Потому что при шаринге хеш кодируется для URL

		let parts = locationHash.split( '?' );
		let path = parts[0];
		let query = decodeURIComponent( parts[1] );
		let get = this.parseParams( query );

		return {
			'hash': locationHash,
			'path': path,
			'query': query ? query : null,
			'get': get,
			'dir': path.substr( 1 )
		};
	}

	parseParams = ( query ) => {
		var get = {};

		if ( query ) {
			query.split( '&' ).forEach( function ( item ) {
				var p = item.split( '=' );
				get[p[0]] = p[1];
			} );
		}

		return get;
	}

	navigate = ( to ) => {
		this._setHash( to );
	}

	_setHash = ( newHash ) => {
		let currentHashData = this.data;
		let newHashData = this.parseHash( newHash );
		console.log( 'Router:', 'Navigate to', newHash, newHashData, 'PrevData', currentHashData );

		console.log( 'Router: Push state' );
		window.history.pushState( {}, '', '#' + newHash  );

		this.data = newHashData;
		Connect.send( "VKWebAppSetLocation", {"location": newHash} );

		this._sendEvent( 'RouterHashChanged', {data: this.data} );
	}

	_sendEvent = ( type, detail ) => {
		let event = new CustomEvent( type, {
			bubbles: true,
			cancelable: true,
			detail: detail
		} );

		window.dispatchEvent( event );
	}
}


export default Router;