import React from 'react';
import './Preloader.css';


export default class Preloader extends React.Component {
	constructor( props ) {
		super( props );
	}

	componentDidMount() {
		this.props.toggle( false );
	}

	componentDidUpdate() {
		if ( this.props.active ) this.props.toggle( false );
	}

	render() {
		return (
			<div id="preloader" className={this.props.active && "preloader--active"} />
		);
	}
}