import React from 'react';
import { Panel, Slider, Touch } from '@vkontakte/vkui';
import { Header, Content, PlayerViewCover, H, Subtitle, Center, PlayerButtons, PlayerButton, PlayerVolume, PlayerControls, PlayerLayout } from '../components/index.js';

import Menu from '../modals/Menu/Menu';
import Menus from '../Menus.js';
import Actions from '../Actions.js';


class PlayerView extends React.Component {
	constructor( props ) {
		super( props );

		this.state = {
			volume: props.audioPlayer._getState().volume
		};

		this.Actions = new Actions( props.audioPlayer, this );
		this.Menus = new Menus( this.Actions );

		this.lastStation = props.station;
	}

	componentDidMount() {
		this.updateURI();
	}

	componentDidUpdate() {
		// Если изменилась радиостанция, то обновляем URI и сохраняем
		if ( this.lastStation.id != this.props.station.id ) {
			this.updateURI();
		}

		this.lastStation = this.props.station;
	}

	updateURI = () => {
		let uri = '/player?station=' + this.props.station.id;
		this.props.navigate( uri );
	}

	changeVolume = ( volume ) => {
		this.setState( {
			volume: volume
		} );

		this.props.setVolume( volume );
	}


	onSwipeStart = ( event ) => {
		if ( event.originalEvent.type == 'mousedowm' ) return;

		this.cover = document.getElementById( 'cover' );
		this.cover.classList.remove( 'c-player__cover--animate' );
	}

	onSwipe = ( event ) => {
		if ( event.originalEvent.type == 'mousemove' ) return;
		if ( Math.abs( event.shiftX ) < 10 ) return;

		let opacity = Math.abs( 1 / event.shiftX * 10 );

		this.cover.style.transform = 'translateX( ' + event.shiftX + 'px )';
		this.cover.style.opacity = opacity;
	}

	onSwipeEnd = ( event ) => {
		if ( event.originalEvent.type == 'mouseup' ) return;

		if ( Math.abs( event.shiftX ) > 10 ) {
			this.cover.classList.add( 'c-player__cover--animate' );
			this.cover.style.transform = 'translateX( 0px )';
			this.cover.style.opacity = 1;
		}

		if ( event.shiftX > 60 ) this.props.prev();
		else if ( event.shiftX < -60 ) this.props.next();
	}

	openMenu = () => {
		var id = this.props.station.id;
		console.log( id );

		var menu = [
			this.Menus.station.update,
			this.Actions.isFavorite( id ) ? this.Menus.station.del_from_favorite : this.Menus.station.to_favorite,
			this.Menus.station.group,
			this.Menus.share,
			this.Actions.isHidden( id ) ? this.Menus.station.show : this.Menus.station.hide,
		];

		this.props.openModal( <Menu items={menu} data-id={this.props.station.id} onClose={this.closeModal} />, this.closeModal );
	}

	closeModal = () => {
		this.props.onModalClose();
	}

	closeModal = () => {
		this.props.onModalClose();
	}

	showNotice = ( text ) => {
		this.props.showNotice( text );
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<Header onClick={this.props.goBack } icon="browser_back">Плеер</Header>
				<Content>
					<PlayerLayout>
						<Touch onStart={this.onSwipeStart}
								onMove={this.onSwipe}
								onEnd={this.onSwipeEnd}>
							<PlayerViewCover id="cover" cover={this.props.station.img} />
						</Touch>
						
						<PlayerControls>
							<H level="2">{this.props.station.name}</H>
							<Subtitle>{this.props.station.genre}</Subtitle>
							<PlayerButtons>
								<PlayerButton icon="list" width="10" smallIcon={true} onClick={this.props.navigate} data-to="/main" />
								<PlayerButton icon="skip_previous" width="20" onClick={this.props.prev} />
								<PlayerButton icon={this.props.paused ? 'play' : 'pause'} width="20" onClick={this.props.toggle} />
								<PlayerButton icon="skip_next" width="20" onClick={this.props.next} />
								<PlayerButton icon="more_vertical" width="10" onClick={this.openMenu} smallIcon={true} />
							</PlayerButtons>

							{window.platform != 'ios' && 
								<PlayerVolume changeVolume={this.changeVolume}>
									<Slider
						                step={0.05}
						                min={0}
						                max={1}
						                value={Number(this.state.volume)}
						                onChange={this.changeVolume}
						                top="Громкость"
						              />
					            </PlayerVolume>
					        }
					    </PlayerControls>
					</PlayerLayout>
				</Content>
			</Panel>
		);
	}
};


export default PlayerView;
