import React from 'react';
import './Switch.css';


function Switch( { children, name, onChange, ...restProps }) {
	return (
		<div className="c-switch">
			<label className="c-switch__label">
				<span className="c-switch__text">{children}</span>
				<input type="checkbox" className="c-switch__checkbox" name={name} onChange={onChange} {...restProps} />
				<div className="c-switch__switch-label"></div>
			</label>
		</div>
	);
};


export default Switch;