import React from 'react';
import './PlayerButton.css';


export default function PlayerButton( { icon, onClick, width, smallIcon, ...restProps }) {
	return (
		<div className={"c-player__control c-icon--vk"
			+ ( width ? ' c-player__control--' + width : '' )
			+ ( smallIcon ? ' c-player__control--small-icon' : '' )}
			onClick={onClick}
			{...restProps}>
				{icon}
		</div>
	);
}