class AudioPlayer {
	constructor( stations ) {
		this.audio = new Audio();
		
		this.currentStation = {};
		this.currentStationId = 0;
		this.stations = stations;
		this.defaultPlaylist = {};

		this.playlist = []; // array of identifiers of stations
		this.currentPlaylistItemIndex = 0;


		// events

		this.audio.onplay = () => {
			this._sendEvent( 'AudioPlayerPlay', {
				stationId: this.currentStationId
			} );
		}

		this.audio.onpause = () => {
			this._sendEvent( 'AudioPlayerPause', {
				stationId: this.currentStationId
			} );
		}

		this.audio.oncanplay = () => {
			this._sendEvent( 'AudioPlayerCanPlay', {
				stationId: this.currentStationId,
				paused: this.audio.paused
			} );
		}

		this.audio.onvolumechange = ( event ) => {
			this._sendEvent( 'AudioPlayerVolumeChanged', {
				stationId: this.currentStationId,
				volume: this.audio.volume
			} );
		}

		this.audio.onerror = ( event ) => {
			this._sendEvent( 'AudioPlayerError', {
				stationId: this.currentStationId,
				station: this.station
			} );
		}

		this.audio.onwaiting = ( event ) => {
			this._sendEvent( 'AudioPlayerWaiting', {
				stationId: this.currentStationId,
				station: this.station,
				originalEvent: event
			} );
		}


		console.log( 'AudioPlayer:', 'Initialization success' );
		this._sendEvent( 'AudioPlayerInitializationSuccess', {} )
	}

	play = ( id ) => {
		if ( id ) {
			this._set( id );
		}

		this.audio.play();
	}

	pause = () => {
		this.audio.pause();
	}

	toggle = () => {
		if ( this.audio.paused ) {
			this.play();
			return;
		}

		this.pause();
	}

	prev = () => {
		if ( this.playlist.length  ) {
			if ( this.playlist[this.currentPlaylistItemIndex - 1] ) {
				this.play( this.playlist[this.currentPlaylistItemIndex - 1] );
			} else {
				this.play( this.playlist[this.playlist.length - 1] );
			}

			return;
		}

		var keys = Object.keys( this.defaultPlaylist );
		var newId = 1;
		
		keys.forEach( ( id, i ) => {
			if ( this.currentStationId != id ) return;

			var k = i - 1 >= 0 ? i - 1 : keys.length - 1;
			newId = keys[k];
		} );

		this.play( newId );
		this._sendEvent( 'AudioPlayerPrevious', {
			stationId: this.currentStationId
		} );
	}

	next = () => {
		if ( this.playlist.length ) {
			if ( this.playlist[this.currentPlaylistItemIndex + 1] ) {
				this.play( this.playlist[this.currentPlaylistItemIndex + 1] );
			} else {
				this.play( this.playlist[0] );
			}

			return;
		}

		var keys = Object.keys( this.defaultPlaylist );
		var newId = 1;
		
		keys.forEach( ( id, i ) => {
			if ( this.currentStationId != id ) return;

			var k = i + 1 >= keys.length ? 0 : i + 1;
			newId = keys[k];
		} );

		this.play( newId );
		this._sendEvent( 'AudioPlayerNext', {
			stationId: this.currentStationId
		} );
	}

	setVolume = ( volume ) => {
		if ( volume > 1 ) {
			volume = volume / 100;
		}

		this.audio.volume = volume;

		this._sendEvent( 'AudioPlayerVolumeChanged', {
			volume: volume
		} );
	}

	_getState = () => {
		return {
			paused: this.audio.paused,
			currentStationId: this.currentStationId,
			station: this.station,
			volume: this.audio.volume,
			playlist: this.playlist
		};
	}

	_sendEvent = ( type, detail ) => {
		let event = new CustomEvent( type, {
			bubbles: true,
			cancelable: true,
			detail: detail
		} );

		window.dispatchEvent( event );
	}

	_clearPlaylist = () => {
		this.playlist = [];
		this.currentPlaylistItemIndex = -1;

		console.log( 'AudioPlayer current playlist is empty.' );
	}

	_setPlaylist = ( playlist ) => {
		if ( !playlist ) return;

		this.playlist = playlist;

		var index = this.playlist.indexOf( parseInt( this.currentStationId ) );
		if ( index !== -1 ) {
			this.currentPlaylistItemIndex = index;
		}

		this._sendEvent( 'AudioPlayerSetPlaylist', {
			playlist: this.playlist
		} );


		//console.log( 'AudioPlayer new playlist:', this.playlist );
	}

	_setDefaultPlaylist = ( defaultPlaylist ) => {
		this.defaultPlaylist = defaultPlaylist;
	}

	_set = ( id ) => {
		if ( !this.stations[id] ) {
			console.warn( 'AudioPlayer:', 'Unknown station with ID', id );
			return;
		}

		if ( this.playlist.indexOf( parseInt( id ) ) != -1 ) {
			this.currentPlaylistItemIndex = this.playlist.indexOf( parseInt( id ) );
		}

		this.currentStationId = id;
		this.station = this.stations[id];

		this.audio.src = this.station.src;

		this._sendEvent( 'AudioPlayerSetStation', {
			stationId: this.currentStationId,
			station: this.station
		} );
	}
}


export default AudioPlayer;