import React from 'react';
import { ModalRoot, Modal, Category, PrimaryButton, H, Switch, Select, InputNote } from '../../components/index.js';

import appLogo from '../../images/150.jpg';

import LocalStorage from '../../LocalStorage.js';
import Theme from '../../Theme.js';
import ExternalLink from '../../ExternalLink.js'

export default class Settings extends React.Component {
	constructor( props ) {
		super( props );

		var settings = LocalStorage.get( 'settings' );

		try {
			settings = JSON.parse( settings );
		} catch( e ) {}

		if ( settings ) this.state = settings;
		else {
			this.state = {
				theme: 'client_light',
				show_hidden: false,
				auto_theme: true
			};
		}
	}

	componentDidMount() {
		var settings = LocalStorage.getJSON( 'settings' );
		this.setState( settings );
	}

	componentWillUnmount() {
		LocalStorage.saveJSON( 'settings', this.state );
	}

	componentDidUpdate() {
		LocalStorage.saveJSON( 'settings', this.state );


		if ( !this.state.auto_theme && this.state.theme == 'client_dark' ) {
			Theme.set( 'client_dark' );
		} else if ( this.state.auto_theme ) {
			Theme.set( window.theme );
		} else {
			Theme.set( this.state.theme );
		}


		this._sendEvent( 'SettingsSaved', this.state );
	}

	closeModal = () => {
		this.props.onClose();
	}

	formChange = ( event ) => {
		this.setState( {
			theme: event.target.value
		} );
	}

	onChange = ( e ) => {
		var newState = {};
		var ct = e.currentTarget;

		newState[ct.name] = ct.checked != undefined ? ct.checked : ct.value;
		this.setState( newState );
	}

	_sendEvent = ( type, detail ) => {
		let event = new CustomEvent( type, {
			bubbles: true,
			cancelable: true,
			detail: detail
		} );

		window.dispatchEvent( event );
	}

	donate = () => {
		var link = 'https://vk.com/app6471849_-162510713';
		ExternalLink.open( link );
	}

	render() {
		return (
			<ModalRoot position="bottom" onClose={this.closeModal} opened={true}>
				<Modal>
					<H level="3">Настройки</H>
					<Switch name="show_hidden" checked={this.state.show_hidden} onChange={this.onChange}>Показывать радиостанции, которые вы скрыли</Switch>
					<Switch name="auto_theme" checked={this.state.auto_theme} onChange={this.onChange}>Автоматический выбор темы оформления</Switch>
					<p>Тема оформления по умолчанию:</p>
					<Select onChange={this.onChange} name="theme" value={this.state.theme} disabled={this.state.auto_theme}>
						<option value="client_light">Светлая</option>
						<option value="client_dark">Тёмная</option>
					</Select>

					<InputNote>Будет включена, если отключить пункт "Автоматический выбор темы оформления"</InputNote>

					<Category title="Отправить деньги"
							  subtitle="На развитие сервиса"
							  left="money_transfer"
							  iconColor="black"
							  nopad="true"
							  leftType="icon"
							  hide="hide-item--ios"
							  onClick={this.donate}
					 />

					<Category title="О приложении"
							  subtitle="Информация о приложении"
							  additionalIcon="like"
							  additionalIconColor="red"
							  left={appLogo}
							  nopad="true"
							  leftType="img"
							  onClick={this.props.navigate}
							  data-to="/about"
					/>
				</Modal>
			</ModalRoot>
		);
	}
}