import React from 'react';
import './Notice.css';


export default class Notice extends React.Component {
	constructor( props ) {
		super( props );

		this.state = {
			active: false,
			text: ''
		};
	}

	close = () => {
		clearTimeout( this.timeout );

		this.setState( {
			active: false,
			text: ''
		} );


		if ( typeof this.props.onClose === 'function' ) {
			this.props.onClose();
		}
	}

	componentDidUpdate() {
		if ( this.props.isActive === false && this.state.active ) {
			this.close();
		}


		//this.state.active != this.props.isActive && 
		if ( this.state.text != this.props.text ) {
			clearTimeout( this.timeout );

			if ( this.state.active != this.props.isActive ) {
				this.setState( {
					active: true,
					text: this.props.text
				} );
			}


			this.timeout = setTimeout( () => {
				this.close();
			}, 5000 );
		}
	}

	componentWillUnmount() {
		clearTimeout( this.timeout );
	}

	render() {
		return (
			<div id="notice" className={this.state.active ? 'notice--active' : '' + !this.state.active ? ' notice_hide' : ''} onClick={this.close}>
				<div className="notice_close" />
				{this.props.text}
			</div>
		);
	}
}