import React from 'react';
import './Input.css';


export default function Input( { placeholder, onChange, onInput, type, name, icon, iconColor, onFocus, ...restProps }) {
	return (
		<div className={"c-input" + ( icon ? ' c-input--with-icon c-input--with-icon-nopad' : '') }>
			{icon && <div className={"c-input__icon c-icon--vk" + ( icon ? ' c-category__icon--' + iconColor : '')}>
				{ icon }
			</div>}

			<input type={type}
				className="c-input__text"
				name={name}
				placeholder={placeholder}
				onInput={onInput}
				onChange={onChange}
				onFocus={onFocus}
				{...restProps} />
		</div>
	);
}