import Actions from './Actions.js';

class Menus {
	constructor( Actions ) {
		this.Actions = Actions;
	}

	station = {
		play: {
			icon: 'similar',
			iconColor: 'green',
			text: 'Играть',
			onClickClose: true,
			onClick: ( e ) => {this.Actions.play( e ) }
		},

		pause: {
			icon: 'pause',
			iconColor: 'green',
			text: 'Приостановить',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.pause( e ) }
		},

		update: {
			icon: 'replay',
			iconColor: 'brown',
			text: 'Перезагрузить станцию',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.update( e ) }
		},

		to_favorite: {
			icon: 'favorite',
			iconColor: 'blue',
			text: 'Добавить в избранное',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.addToFavorite( e ) }
		},

		del_from_favorite: {
			icon: 'favorite_outline',
			iconColor: 'blue',
			text: 'Удалить из избранного',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.deleteToFavorite( e ) }
		},

		group: {
			icon: 'users',
			iconColor: 'orange',
			text: 'Сообщество радиостанции',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.group( e ) }
		},

		hide: {
			icon: 'cancel',
			iconColor: 'red',
			text: 'Скрыть',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.addToHidden( e ) }
		},

		show: {
			icon: 'view',
			iconColor: 'brown',
			text: 'Не скрывать',
			onClickClose: true,
			onClick: ( e ) => { this.Actions.deleteFromHidden( e ) }
		}
	}

	share = {
		icon: 'share_outline',
		iconColor: 'purple',
		text: 'Поделиться радиостанцией',
		onClickClose: false,
		onClick: ( e ) => {this.Actions.openShare( e )}
	}

	showQRCode = {
		icon: 'qr',
		iconColor: 'blue',
		text: 'Показать QR-код',
		onClickClose: false,
		onClick: ( e ) => {this.Actions.showPlayerQRCode( e )}
	}

	wallpost = {
		icon: 'newsfeed',
		iconColor: 'green',
		text: 'Опубликовать пост на стене',
		onClickClose: false,
		onClick: ( e ) => { this.Actions.wallpost( e ) }
	}

	shareOther = {
		icon: 'share_outline',
		iconColor: 'brown',
		text: 'Поделиться',
		onClickClose: false,
		onClick: ( e ) => { this.Actions.shareOther( e ) }
	}
};

export default Menus;