import React from 'react';
import './Player.css';
import { Category } from '../../components/index.js';
import appLogo from '../../images/150.jpg';


export default function Player( { icon, title, right, onRightIconClick, subtitle, onItemClick, ...restProps } ) {
	return (
		<div className="player">
			<Category title={title}
					subtitle={subtitle}
					left={icon} leftType="img"
					onItemClick={onItemClick}
					right={right}
					onRightIconClick={onRightIconClick}
					{...restProps}
					/>
		</div>
	);
}