import React from 'react';
import './EmptyFiller.css';


export default function EmptyFiller( { icon, text, ...restProps } ) {
	return (
		<div className="c-empty" {...restProps}>
			<div className="c-empty__icon c-icon--vk">{icon}</div>
			<div className="c-empty__text">{text}</div>
		</div>
	);
}