import React from 'react';
import './Category.css';


export default function Category ( { title, subtitle, left, leftType, iconColor, right, onRightIconClick, onItemClick, additionalIcon, additionalIconColor, nopad, hide, ...restProps } ) {
	return (
		<div className={'c-category' + ( right ? ' c-category--with-button' : '' ) + ( nopad ? ' c-category--small-pad' : '' ) + ( hide ? ' ' + hide : '' )}>
			<span className="c-category__click-area" onClick={onItemClick} {...restProps}>
				{ leftType == 'img' &&
					<div className="c-category__icon c-category__icon--image" style={{background: 'url( "' + left + '" )'}}></div> }

				{ leftType == 'icon' &&
					<div className={ 'c-category__icon c-icon--vk' + ( iconColor ? ' c-category__icon--' + iconColor : '' ) }>
						{ left }
					</div> }

				{ additionalIcon && <div className={"c-category__icon--additional c-icon--vk" + 
					( additionalIconColor ? ' c-category__icon--' + additionalIconColor : '' ) }>{ additionalIcon }</div> }
				<div className="c-category__text-content">
					<div className="c-category__title">{ title }</div>
					<div className="c-category__text">{ subtitle }</div>
				</div>
			</span>

			{ right && <div className="c-icon--vk c-category__button" onClick={onRightIconClick}>{ right }</div> }
		</div>
	);
}