import React from 'react';
import { Panel } from '@vkontakte/vkui';
import { Header, Content, Category, CategoryList, Input, Hr, HQ } from '../components/index.js';


import appLogo from '../images/150.jpg';

import Settings from '../modals/Settings/Settings';
import Stations from './../Stations.js';

import ModalSettings from '../modals/Settings/Settings';

import Menu from '../modals/Menu/Menu';
import Menus from '../Menus.js';

import Actions from '../Actions.js';

function compareArrays( arr1, arr2 ) {
	var res = true;

	arr1.forEach( ( item, i ) => {
		res = res && item === arr2[i];
	} );

	return res;
}

class Main extends React.Component {
	constructor( props ) {
		super( props );

		if ( !compareArrays( props.audioPlayer._getState().playlist, window.favorite ) ) {
			props.clearPlaylist();
		}

		this.Actions = new Actions( props.audioPlayer, this );
		this.Menus = new Menus( this.Actions );

		this.state = {
			favoriteIds: []
		};

		this.stations = {};
		this.favorite = {};

		this.prepare( () => {
			this.StationsList = this.build( this.stations );
			this.Favorite = this.build( this.favorite );
		} );
	}

	prepare = ( callback ) => {
		this.stations = {};
		this.favorite = {};


		for ( var stationId in Stations ) {
			var station = Stations[stationId];

			if ( !this.props.showHidden && this.props.hidden.indexOf( parseInt( station.id ) ) !== -1 ) {
				continue;
			}

			if ( this.props.hidden.indexOf( parseInt( station.id ) ) !== -1 ) {
				station.hidden = true;
			} else {
				delete station.hidden;
			}

			var favIndex = this.props.favorite.indexOf( station.id );

			if ( favIndex !== -1 ) {
				station.favorite = true;
				this.favorite[favIndex] = station;

				continue;
			} else {
				delete station.favorite;
			}

			this.stations[station.id] = station;
		}

		if ( typeof callback === 'function' ) callback();
	}


	build = ( stationsList ) => {
		return Object.keys( stationsList ).map( ( station ) => {
			var s = stationsList[station];



			if ( s.favorite ) {
				var additionalIcon = 'favorite';
				var additionalIconColor = 'blue';
			}

			if ( this.props.station ) {
				if ( s.id == this.props.station.id ) {
					var additionalIcon = this.props.isPlaying ? 'pause' : 'play';
					var additionalIconColor = 'green';
				}
			}

			if ( s.hidden ) {
				var additionalIcon = 'hide';
				var additionalIconColor = 'red';
			}
			

			var menu = [
				s.id == this.props.station.id && this.props.isPlaying ? this.Menus.station.pause : this.Menus.station.play,
				s.id == this.props.station.id ? this.Menus.station.update : null,
				this.Actions.isFavorite( s.id ) ? this.Menus.station.del_from_favorite : this.Menus.station.to_favorite,
				this.Menus.station.group,
				this.Menus.share,
				this.Actions.isHidden( s.id ) ? this.Menus.station.show : this.Menus.station.hide,
			];

			var subtitle = s.is_hq? <><HQ />{s.genre}</> : s.genre;

			return {
				id: s.id,
				title: s.name,
				subtitle: subtitle,
				icon: s.img,
				iconType: 'img',
				right: 'more_horizontal',
				onClick: s.id == this.props.station.id ? this.props.toggle : this.setStation,
				onRightIconClick: () => this.props.openModal( <Menu items={menu} data-id={s.id} onClose={this.closeModal} />, this.closeModal ),
				additionalIcon: additionalIcon,
				additionalIconColor: additionalIconColor
			}
		} );
	}

	componentDidUpdate() {
		if ( this.props.favorite != this.state.favoriteIds ) {
			this.setState( {
				favoriteIds: this.props.favorite
			} );
		}

		// Если начальная станция в избранном, то добавляем плейлист
		if ( this.Actions.isFavorite( this.props.station.id ) ) {
			this.props.setPlaylist( window.favorite );
		}
	}

	openSettings = () => {
		this.props.openModal( ModalSettings, this.props.onModalClose, {navigate: this.props.navigate} );
	}

	closeModal = () => {
		this.props.onModalClose();
	}

	setStation = ( e ) => {
		// чтобы если в избранном запускать плейлист с избранным
		var id = e.currentTarget.dataset.id;

		// Чтобы в общем списке, если выбрать станцию из избранного, кнопки далее и назад работали только по избранному
		// А если не из избранного, то чистим плейлист и навигация будет по общему списку
		if ( this.Actions.isFavorite( id ) ) {
			this.props.setPlaylist( window.favorite );
		} else {
			console.log( 'Clearing playlist' );
			this.props.setPlaylist( [] );
		}

		this.props.setStation( e );
	}


	render() {
		this.prepare( () => {
			this.StationsList = this.build( this.stations );
			this.Favorite = this.build( this.favorite );
		} );

		return (
			<Panel id={this.props.id}>
				<Header icon="filter" onClick={this.openSettings}>Радио</Header>
				<Content>
					<Input name="test" placeholder="Поиск" icon="search_outline" iconColor="black" onFocus={this.props.navigate} data-to="/search" />

					
					{Object.keys( this.Favorite ).length > 0 && (<><CategoryList items={this.Favorite} data-to="player" />
																	   <Hr /></>
																)}
					
					<CategoryList items={this.StationsList} data-to="player" />
				</Content>
			</Panel>
		);
	}
};


export default Main;
