import React from 'react';
import './Header.css';

export default class Header extends React.Component {
	constructor( props ) {
		super( props );
	}

	render() {
		return (
			<div className="l-panel">
				<div className="c-panel__back c-icon--vk" onClick={this.props.onClick}>{ this.props.icon }</div>
				<div className="c-panel__title">{ this.props.children }</div>
			</div>
		);
	}
}