import React from 'react';
import './Select.css';


function Select( { children, name, value, onChange, ...restProps }) {
	return (
		<div className="c-input-select">
			<select name={name} className="c-select__select" value={value} onChange={onChange} {...restProps}>
				{children}
			</select>
			<div className="c-input-select__dropdown-icon c-icon--vk">dropdown</div>
		</div>
	);
};


export default Select;