import React from 'react';
import { ModalRoot, Modal, Category, SecondaryButton, H, EmptyFiller } from '../../components/index.js';


export default class GroupNone extends React.Component {
	constructor( props ) {
		super( props );

		this.state = {
			opened: true
		};
	}

	closeModal = () => {
		this.props.onClose();
	}

	close = () => {
		this.setState( { opened: false } );
	}

	formChange = ( event ) => {
		this.setState( {
			theme: event.target.value
		} );
	}

	render() {
		return (
			<ModalRoot position="bottom" onClose={this.closeModal} opened={this.state.opened}>
				<Modal>
					
					<EmptyFiller icon="similar" text="К сожалению, мы пока не добавили сообщество для этой радиостанции, либо его и вовсе нет." />

					<SecondaryButton onClick={this.close} wide={true}>Закрыть</SecondaryButton>
				</Modal>
			</ModalRoot>
		);
	}
}