import React from 'react';
import PropTypes from 'prop-types';
import './CategoryList.css';

import { Category } from '../../components/index.js';

function CategoryList ( { items, children, ...restProps }) {
	const StationsList = items.map( ( item, i ) => {
		let id = item.id ? item.id : i;

		return (
			<Category title={item.title}
					  data-id={id}
					  key={id}
			          subtitle={item.subtitle}
			          left={item.icon}
			          leftType={item.iconType}
			          onClick={item.onClick}
			          right={item.right}
			          onRightIconClick={item.onRightIconClick}
			          additionalIcon={item.additionalIcon}
			          additionalIconColor={item.additionalIconColor}
			          data-data={JSON.stringify( item.data )}
			          { ...restProps }
			/>
		);
	} );

	return (
		<div className="l-category-list">
			{ StationsList} 
			{ children }
		</div>
	);
}


CategoryList.propTypes = {
	items: PropTypes.array.isRequired
};

export default CategoryList;