import React from 'react';
import { ModalRoot, Modal, H, MenuItem } from '../../components/index.js';

import appLogo from '../../images/150.jpg';

export default class Menu extends React.Component {
	constructor( props ) {
		super( props );

		this.id = props['data-id'];

		this.state = {
			opened: true
		};

		this.close.bind( this );
	}

	closeModal = () => {
		this.props.onClose();
	}

	close = () => {
		this.setState( {opened: false} );
	}

	render() {
		const Items = this.props.items.map( ( item, i ) => {
			if ( !item ) return;
			
			return (
				<MenuItem
					key={i}
					icon={item.icon}
					iconColor={item.iconColor}
					text={item.text}
					onClick={item.onClick}
					close={this.close}
					needClose={item.onClickClose}
					data-id={this.id}
				/>
			)
		} );

		return (
			<ModalRoot position="bottom" onClose={this.closeModal} opened={this.state.opened}>
				<Modal>
					<H level="3">Действие</H>
					<br />

					{ Items }
				</Modal>
			</ModalRoot>
		);
	}
}