import React from 'react';
import './SecondaryButton.css';


export default class SecondaryButton extends React.Component {
	constructor( props ) {
		super( props );
	}

	render() {
		return (
			<button onClick={this.props.onClick} className={'c-button c-button--secondary' + ( this.props.wide ? ' c-button--wide' : '' )}>
				{ this.props.children }
			</button>
		);
	}
}