import connect from '@vkontakte/vkui-connect';
import Config from './Config.js';

var Theme = {
	dark: {
		action_bar: { "status_bar_style": "light", "action_bar_color": "none" },
		popup_action_bar: { "status_bar_style": "light", "action_bar_color": "none" }
	},

	light: {
		action_bar: { "status_bar_style": "dark", "action_bar_color": "none" },
		popup_action_bar: { "status_bar_style": "light", "action_bar_color": "none" }
	},

	set: function( theme ) {
		var action_bar = {};

		if ( theme == 'client_dark' || theme == 'space_gray' ) {
			window.currentTheme = 'client_dark';

			document.body.classList.add( 'theme-dark' );
			Config.action_bar = Theme.dark.action_bar;
			Config.popup_action_bar = Theme.dark.popup_action_bar;
		} else {
			window.currentTheme = 'client_light';

			document.body.classList.remove( 'theme-dark' );
			Config.action_bar = Theme.light.action_bar;
			Config.popup_action_bar = Theme.light.popup_action_bar;
		}

		if ( connect.supports( 'VKWebAppSetViewSettings' ) ) {
			connect.send( 'VKWebAppSetViewSettings', Config.action_bar );
		}
	},

	setPopupActionBar: function () {
		if ( window.currentTheme == 'client_dark' ) {
			var popup_action_bar = Theme.dark.popup_action_bar;
		} else {
			var popup_action_bar = Theme.light.popup_action_bar;
		}

		if ( connect.supports( 'VKWebAppSetViewSettings' ) ) {
			connect.send( 'VKWebAppSetViewSettings', popup_action_bar );
		}
	},

	setActionBar: function () {
		if ( window.currentTheme == 'client_dark' ) {
			var action_bar = Theme.dark.action_bar;
		} else {
			var action_bar = Theme.light.action_bar;
		}

		if ( connect.supports( 'VKWebAppSetViewSettings' ) ) {
			connect.send( 'VKWebAppSetViewSettings', action_bar );
		}
	}
};

export default Theme;