import React from 'react';
import { Panel,  } from '@vkontakte/vkui';
import { Header, Content, Category, CategoryList, Input, Hr, HQ, EmptyFiller } from '../components/index.js';


import appLogo from '../images/150.jpg';

import Settings from '../modals/Settings/Settings';
import Stations from './../Stations.js';

import Actions from './../Actions.js';

import Menu from '../modals/Menu/Menu';
import Menus from '../Menus.js';

class Main extends React.Component {
	constructor( props ) {
		super( props );
		this.station = props.station;

		this.Actions = new Actions( props.audioPlayer, this );
		this.Menus = new Menus( this.Actions );

		if ( props.state && Object.keys( props.state ).length > 0 ) {
			this.state = props.state;

			this.prepare();
			this.prepareStationsList();
		} else {
			this.state = {
				findedStations: {},
				StationsList: [],
				playlist: [],
				station: this.props.station,
				search_str: ''
			};
		}

		this.StationsList = [];
	}


	prepareStationsList = () => {
		if ( !this.stations ) {
			this.StationsList = [];
			return;
		}

		var currentStationId = this.props.station.id;
		this.playlist = [];

		this.StationsList = Object.keys( this.stations ).map( ( station ) => {
			var s = Stations[station];

			if ( s.favorite ) {
				var additionalIcon = 'favorite';
				var additionalIconColor = 'blue';
			}

			if ( this.props.station ) {
				if ( s.id == this.props.station.id ) {
					var additionalIcon = this.props.isPlaying ? 'pause' : 'play';
					var additionalIconColor = 'green';
				}
			}

			if ( s.hidden ) {
				var additionalIcon = 'hide';
				var additionalIconColor = 'red';
			}

			var menu = [
				s.id == this.props.station.id && this.props.isPlaying ? this.Menus.station.pause : this.Menus.station.play,
				s.id == this.props.station.id ? this.Menus.station.update : null,
				this.Actions.isFavorite( s.id ) ? this.Menus.station.del_from_favorite : this.Menus.station.to_favorite,
				this.Menus.station.group,
				this.Menus.share,
				this.Actions.isHidden( s.id ) ? this.Menus.station.show : this.Menus.station.hide,
			];


			this.playlist.push( s.id );

			var subtitle = s.is_hq? <><HQ />{s.genre}</> : s.genre;

			return {
				id: s.id,
				title: s.name,
				subtitle: subtitle,
				icon: s.img,
				iconType: 'img',
				right: 'more_horizontal',
				onClick: s.id == this.props.station.id ? this.props.toggle : this.setStation,
				onRightIconClick: () => this.props.openModal( <Menu items={menu} data-id={s.id} onClose={this.closeModal} />, this.closeModal ),
				additionalIcon: additionalIcon,
				additionalIconColor: additionalIconColor
			}
		} );

		this.props.setPlaylist( this.playlist );
	}

	setStation = ( e ) => {
		var id = e.currentTarget.dataset.id;
		this.props.setStation( e );
	}

	prepare = ( callback ) => {
		this.stations = {};		

		for ( var stationId in this.state.findedStations ) {
			var station = Stations[stationId];

			if ( !this.props.showHidden && this.props.hidden.indexOf( station.id ) !== -1 ) {
				continue;
			}

			if ( this.props.hidden.indexOf( station.id ) !== -1 ) {
				station.hidden = true;
			}

			if ( this.props.favorite.indexOf( station.id ) !== -1 ) {
				station.favorite = true;
			} else {
				delete station.favorite;
			}

			this.stations[station.id] = station;
		}



		if ( typeof callback === 'function' ) callback();
	}

	search = () => {
		var search_str = this.state.search_str;
		search_str = search_str.toLowerCase().trim();

		var result = {};

		if ( search_str ) {
			Object.keys( Stations ).forEach( function( id ) {
				var station = Stations[id];

				if ( station.name.toLowerCase().indexOf( search_str ) != -1 || station.genre.toLowerCase().indexOf( search_str ) != -1) {
					result[id] = station;
				}
			});
		}

		this.setState( {
			findedStations: result
		} );
	}

	onChange = ( event ) => {
		this.setState( {
			search_str: event.target.value
		} );
	}

	componentDidMount() {

	}

	componentDidUpdate( prevProps, prevState ) {
		// if ( this.props.station != this.state.station ) {
		// 	this.setState( {station: this.props.station} );

		// 	this.prepare();
		// 	this.prepareStationsList();
		// }

		if ( prevState.search_str != this.state.search_str ) this.search();
	}

	componentWillUnmount() {
		this.props.saveState( 'search', this.state );
	}

	closeModal = () => {
		this.props.onModalClose();
	}


	render() {
		this.prepare();
		this.prepareStationsList();

		return (
			<Panel id={this.props.id}>
				<Header onClick={this.props.goBack} icon="browser_back">Поиск</Header>
				<Content>
					<Input autoFocus={true} id="searchField" placeholder="Поиск" icon="search_outline" iconColor="black" onChange={this.onChange} value={this.state.search_str} />
					{this.StationsList.length == 0 && <EmptyFiller icon="similar" text="Список пуст" />}
					<CategoryList items={this.StationsList} data-to="player" />
				</Content>
			</Panel>
		);
	}
};


export default Main;
